<template>
    <div id = "startStu">
       <el-container height= "100%" direction="vertical">
      <topNav></topNav>
      <el-container >
        <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <flowStu></flowStu>
      </el-aside>
        <el-main>
          <el-row type="flex"  justify="center">
            <el-alert
    :title="status"
    :type="statusicon"
    :closable="false"
            center
            show-icon>
  </el-alert>

          </el-row>
          <el-row style="margin-bottom:20px">


      <el-button v-if="!isnew" type="success" @click="dialogVisible = true" :disabled="haspassed"></el-button>
    <el-tooltip v-show="haspassed" effect="dark" content="" placement="top-start">
      <el-button style="float: right" type="info" icon="el-icon-question" circle></el-button>
    </el-tooltip>



          </el-row>
          <el-dialog
  title=""
  :visible="dialogVisible"
  width="30%"
  :before-close="handleClose">
            <el-row>
            <el-input type="textarea"
  :rows="8" v-model="introduction" placeholder=""></el-input>
              </el-row>
            <el-row >
              <el-divider content-position="left">.rar/.zip/.7z</el-divider>
              <el-upload
                :data="dataf"
                :headers="headerf"
                name = "work"
     accept=".rar, .zip, .7z"
      action='/api/uploadfile/'
      :show-file-list="false"
      :on-success="handlefileSuccess"
      :before-upload="beforefileUpload"
      >
      <el-button size="small" type="primary"></el-button>

</el-upload>
            </el-row>
  <template #footer>
    <span class="dialog-footer">
      <el-button @click="dialogVisible = false"> </el-button>
      <el-button :disabled="uploaded" type="primary" @click="submit"> </el-button>
    </span>
  </template>
</el-dialog>

<el-collapse >
  <el-collapse-item v-for="(item,index) in list" :title="''+ (index+1) +''">
    <span class="check">{{item.content}}</span>
    <br>
    <el-button size="mini"></el-button>
    <el-button size="mini"></el-button>
  </el-collapse-item>

</el-collapse>

        </el-main>
      </el-container>
    </el-container>
    </div>
</template>

<script>
  import topNav from '../components/topNav'
import flowStu from '../components/flowStu'

    export default {
        name: "mediumStu",
      components: {
      topNav,
      flowStu,
    },
      data(){
          return {
      dataf:{},
            headerf:{},
            title:"",
            workid:"",
            recordid:"",
            watchId:"",
            role:"",
            dialogVisible:false,
            uploaded:true,
            haspassed:false,
            status:"",
            statusicon:"",
            introduction:"",
            list:[
              // {content:""},
              // {content:""},
              // {content:""}
            ],
            isnew:""
  }
      },
      created(){

        this.getCurUserID()
      },
    methods: {

      changetitle(){
            console.log("TO DO");
      },
 beforefileUpload(file) {

    },
  handlefileSuccess(response, file, fileList) {
   // console.log(response);
   // console.log(file.raw);
    this.$http.post('/api/addRecordStu/',{'stuId':this.watchId,'workId':this.workid,'path':response['path'],'process':'M'},{emulateJSON:true})
              .then(function(response){
                    var res1 = JSON.parse(response.bodyText);
                    if(res1['err_code']==0) {
                      this.dataf['next'] = this.dataf['next'] + 1;
                      this.recordid = res1["recordid"];
                      this.$message({
                      type: 'info',
                      message: ''
                        });
                      this.uploaded = false;
                    }
                    else{
                      this.$message.error(",")
                    }

              })

    },
      getCurUserID(){
        this.$http.get('/api/getCurUserID')
                .then((response) => {
                  var res1 = JSON.parse(response.bodyText)
                  if (res1['err_num'] == 0) {
                    this.watchId = res1['userID'];
                    this.role = res1['role'];
                    this.getdata();
                  }
                })
      },
      getstatus(){
            this.$http.post('/api/getStudentMediumProcess/',{'stuId':this.watchId},{emulateJSON:true})
              .then(function(response){
                 var res1 = JSON.parse(response.bodyText);
                this.status = res1["info"];
                 this.statusicon = res1["type"];
                 if (res1["statuscode"] >= 70 || res1["statuscode"]<40){
                   this.haspassed = true;
                 }
              })
      },
      getdata(){
        //     record(list)title
        // 
            this.$http.post('/api/getRecordTitle/',{'stuId':this.watchId,'process':'M'},{emulateJSON:true})
              .then(function(response){
                    var res1 = JSON.parse(response.bodyText);
                    if(res1['err_code'] ==0) {
                      if(res1['title'] == "null"){
                        this.isnew = true;
                      }else{
                        this.isnew = false;
                        this.title = res1['title'];
                        this.workid = res1['workid'];
                      }
                      for (var i = 0;i<res1['rlist'].length;i++){
                        this.list.push({content:res1['rlist'][i]['fields']['content']})
                      }
                      this.dataf['next'] = this.list.length + 1;
                      this.dataf['stuid'] = this.watchId;
                      this.dataf['process'] = 'M';
                      this.getstatus();
                    }
                    else{
                      this.$message.error("")
                    }

              })
      },
      handleClose(done) {
        this.$confirm('')
          .then(_ => {
            this.dialogVisible= false;
            location.reload()
            done()
          })
          .catch(_ => {});
      },
      submit(){
            this.$http.post('/api/addRecordIntroduction/',{'recordId':this.recordid,'introduction':this.introduction},{emulateJSON:true})
              .then(function(response){
                    var res1 = JSON.parse(response.bodyText);
                    if(res1['err_code']==0) {
                      this.$message({
                      type: 'info',
                      message: ''
                        });
                      location.reload()
                    }
                    else{
                      this.$message.error(",")
                    }

              })

      }

    }
    }
</script>

<style scoped>
.check{
  font-size: medium;
}
  /*#alert{*/
     /*box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);*/
    /**/
  /*}*/
</style>




