<template>
  <div id="personinfo" >
    <el-row type="flex" justify="center">
      <el-image :src= "require('@/assets/icon.jpg')" fit="fill"></el-image>
    </el-row>
    <el-row type="flex" justify="center">
    <el-col :span="8" type="flex" justify="start">
  <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" >
    <el-form-item label="" prop="name" >
    <el-input placeholder="" v-model="ruleForm.name" autocomplete="off" clearable :readonly="editable"></el-input>
  </el-form-item>
    <el-form-item label="" prop="username" >
    <el-input placeholder="416" v-model="ruleForm.username" autocomplete="off" clearable readonly="true"></el-input>
  </el-form-item>
  <el-form-item label="" prop="uni" >
    <el-input placeholder="" v-model="ruleForm.uni" autocomplete="off" clearable readonly="true"></el-input>
  </el-form-item>
  <el-form-item label="" prop="school" >
    <el-input placeholder="" v-model="ruleForm.school" autocomplete="off" clearable readonly="true"></el-input>
  </el-form-item>

    <el-form-item v-if="ruleForm.role==1" label="" prop="code" >
      <el-input  v-model="ruleForm.code" autocomplete="off" clearable readonly="true"></el-input>
    </el-form-item>

    <el-form-item v-if="ruleForm.role==2" label="" prop="req" >
      <el-input  v-model="ruleForm.req" autocomplete="off" clearable :readonly="editable"></el-input>
    </el-form-item>

    <el-form-item label="" prop="email">
    <el-input placeholder="()" v-model="ruleForm.email" autocomplete="off" clearable :readonly="editable"></el-input>
  </el-form-item>
    <el-form-item label="" prop="avater">

      <el-col :span ="10">
<el-avatar v-show="editable" :src=avaterpath  style="margin-left:10%" ></el-avatar>
    <el-upload v-show="!editable"
  class="avatar-uploader"
  name = "avater"
     accept=".jpg, .jpeg, .png"
  action='/api/uploadAvater/'
  :show-file-list="false"
  :on-success="handleAvatarSuccess"
  :before-upload="beforeAvatarUpload">
  <!--<img v-if="avaterpath" :src=avaterpath class="avatar">-->
  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
</el-upload>
    </el-col>
      </el-form-item>
    <el-form-item>
      <el-button type="primary" v-show="editable" @click="edit"></el-button>
      <el-button type="primary" v-show="!editable" @click="submitForm('ruleForm')"></el-button>
  </el-form-item>
  <el-form-item>
    <el-link href="/home" type="primary"></el-link>
  </el-form-item>
</el-form>
    </el-col>
      </el-row>
  </div>

</template>

<script>

export default {
  name: 'register',
  components: {},
  data() {
      var validateName = (rule, value, callback) => {
          if (value.length>20) {
            callback(new Error('20'));
          } else if(value.length<=0)
          {
            callback(new Error(''));
          }else {
            callback();
          }

      };
      var validateuni = (rule, value, callback) => {
           if(value.length<=0)
          {
            callback(new Error(''));
          }else {
              callback();
            }
      };
      var validateschool = (rule, value, callback) => {
           if(value.length<=0)
          {
            callback(new Error(''));
          }else {
              callback();
            }
      };
      var validatecode = (rule, value, callback) => {
           if(value.length<=0)
          {
            callback(new Error(''));
          }else {
              callback();
            }
      };
      var validatedefault = (rule, value, callback) => {
              callback();
      };
      return {
        res: '',
        watchId:'',
        editable:true,
        role:'',
        ruleForm: {
          uni:'',
          req:'',
          school:'',
          name:'',
          email:'',
          code:'',
        },
        rules: {
          name: [
            { validator: validateName, trigger: 'blur' }
          ],
          uni: [
            { validator: validateuni, trigger: 'blur' }
          ],
          school: [
            { validator: validateschool, trigger: 'blur' }
          ],
          avater: [
            { validator: validatedefault, trigger: 'blur' }
          ],
          email: [
            { validator: validatedefault, trigger: 'blur' }
          ],
          code: [
            { validator: validatecode, trigger: 'blur' }
          ]
        }
      };
    },
  mounted() {
          this.getCurUserID();
      },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {

            var params = new URLSearchParams();
            // params.append('userName', this.ruleForm.username);
            params.append('name', this.ruleForm.name);
            params.append('email', this.ruleForm.email);
            params.append('avatarpath', this.avaterpath);
            params.append('code', this.ruleForm.code);
            params.append('req', this.ruleForm.req);
            params.append('userId', this.watchId);
            params.append('roleId', this.ruleForm.role);

            this.$axios.post('/api/changeInfo/', params)
            .then(res => {
              this.res = res
                this.$message({
                  type: 'success',
                  message: res.data
                });
              this.editable = true;
            }, err => {
              this.$message({
                type: 'info',
                message: '!\n' + err
              });
            });
          } else {
            console.log('error submit!!');

            this.$message({
                type: 'success',
                message: 'error'
              });
            return false;
          }
        });
      },
      getdata(){
        this.$http.post('/api/getUser/',{'watchId':this.watchId},{emulateJSON:true})
                .then((response) => {
                  var res1 = JSON.parse(response.bodyText)
                  if (res1['err_num'] == 0) {
                    this.ruleForm.name = res1["name"];
                    this.ruleForm.username = res1["username"];
                    this.ruleForm.uni = res1["uni"];
                    this.ruleForm.school = res1["school"];
                    this.ruleForm.email = res1["email"];
                    this.ruleForm.req = res1["req"];
                    this.ruleForm.code = res1["code"];
                  }
                })
      },
      getCurUserID(){
        this.$http.get('/api/getCurUserID')
                .then((response) => {
                  var res1 = JSON.parse(response.bodyText)
                  if (res1['err_num'] == 0) {
                    this.watchId = res1['userID'];
                    this.ruleForm.role = res1['role'];
                    this.avaterpath = res1["avater"];
                    this.getdata();
                  }
                  else{
                      this.$message.error("")
                    }
                })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      handleAvatarSuccess(res, file) {
        this.avater = URL.createObjectURL(file.raw);
        console.log(res);
        if(res['err_code']==0) {
                      this.$message({
                      type: 'info',
                      message: ''
                        });
                      this.avaterpath = res["path"];
                    }
                    else{
                      this.$message.error("")
                    }

      },
      beforeAvatarUpload(file) {
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        return isLt2M;
      },
      edit(){
        this.editable = false;
      }
    }

}
</script>

<style>

</style>



