<template>
<el-header height="60px">
    <div id="topNav" >

      <el-row type="flex"  justify="center" align="middle" >
        <el-col span="20" class="row-tn" >
          <el-avatar v-if="watchId" :src=avatar  style="margin-left:10%" ></el-avatar>

        <el-button type="text"  style="margin-left: 10%"  @click.native="jumpTo($event)" id=""></el-button>
        <el-button type="text" style="margin-left: 10%"  @click.native="jumpTo($event)" id=""></el-button>
        <el-button type="text"  style="margin-left: 10%" @click.native="jumpTo($event)" id=""></el-button>
          <el-button type="text" style="margin-left: 10%"  @click="loginbacth" ></el-button>
        <el-button type="text"   @click="endnow" v-if="hasstart"></el-button>
            <el-button type="text"  style="margin-left: 10%"  @click="dialogVisible2 = true" v-if="!hasstart"></el-button>
<el-button type="primary" @click="toLogin" v-if="!watchId" style="margin-left: 10%"></el-button>
          <el-button type="primary" @click="toLogout" v-if="watchId" style="margin-left: 10%"></el-button>
        </el-col>

        </el-row>
      <el-dialog
  title=""
  :visible="dialogVisible"
  width="30%"
  :before-close="handleClose">

              <span><br>
                1.<br>
                2.ABAB<br>
                3.<br>
              4.</span>
<el-row>
            <el-input  v-model="suffix" placeholder=""></el-input>
              </el-row>
            <el-row >
              <el-divider content-position="left">.xlsxlsx</el-divider>
              <el-upload
                :data="dataf"
                :headers="headerf"
                name = "loginsheet"
     accept=".xls, .xlsx"
      action='/api/loginBacth/'
      :show-file-list="false"
      :on-success="handlefileSuccess"
      :before-upload="beforefileUpload"
      >
      <el-button size="small" type="primary"></el-button>

</el-upload>
            </el-row>
  <!--<template #footer>-->
    <!--<span class="dialog-footer">-->
      <!--<el-button @click="dialogVisible = false"> </el-button>-->
      <!--<el-button :disabled="uploaded" type="primary" @click="submit"> </el-button>-->
    <!--</span>-->
  <!--</template>-->
</el-dialog>
        <el-dialog
  title=""
  :visible="dialogVisible2"
  width="30%"
  :before-close="handleClose2">

              <span></span>
<el-row>
            <el-input  v-model="count"></el-input>
              </el-row>

            <el-button type="primary" @click="startnow"> </el-button>
</el-dialog>
    </div></el-header>
</template>

<script>

/*
 * topNav
 * 
 */

export default {

  name: 'manageNav',
  props:{
    watchId:{
      type: String,
      default: ''
    }
  },
  components: {
  },
    mounted: function(){
        this.getCurUserID();

    },
    data(){
      return {
          input1:'',
          avatar:'',
          role:'',
        dialogVisible:false,
          dialogVisible2:false,
        dataf:{},
         headerf:{},
        suffix:'',
        watchId:'',
          hasstart:false,
          count:'',
    }
    },

  methods:{
    getCurUserID(){
        this.$http.get('/api/getCurUserIDMan')
                .then((response) => {
                  var res1 = JSON.parse(response.bodyText)
                  if (res1['err_num'] == 0) {
                    this.watchId = res1['userID'];
                    this.role = res1['role'];
                    if (res1['avater'] == 0){
                        this.hasstart = false;
                        this.count = res1['count'];
                    }
                    else{
                        this.hasstart = true
                    }
                  }
                })
      },
    loginbacth(){
      this.dialogVisible = true;
    },
      jumpTo: function(e){
          var name=e.currentTarget.id;
          if(name==""){
              window.location.href="/home";
          }
          else if(name==""){
                    window.location.href="checkMan";

          }
          else if(name==""){
                  window.location.href="/fileMan";
          }
      },
      checkMessage: function() {
          if(this.userId==0){
                  this.$message({type:'error',message:"",duration:600})
              }
          else{
              window.location.href="/message";
          }
      },
      toLogin: function () {
          window.location.href = "/login";      },
      toLogout: function () {
         this.$http.get('/api/logout')
                .then((response) => {
                  var res1 = JSON.parse(response.bodyText)
                  if (res1['err_code'] == 0) {
                    location.reload()
                  }
                })
      },
    handleClose(done) {
        this.$confirm('')
          .then(_ => {
            this.dialogVisible= false;
            location.reload()
            done()
          })
          .catch(_ => {});
      },
      handleClose2(done) {
        this.$confirm('')
          .then(_ => {
            this.dialogVisible2= false;
            done()
          })
          .catch(_ => {});
      },
    handlefileSuccess(response, file, fileList){
      console.log(response['err_code'])
      if (response['err_code'] == 0) {
        this.dialogVisible = false;
                    this.$message({
                      type: 'success',
                      message: ''
                        });
                  }
      else if (response['err_code'] == 1){
        this.$message({
                      type: 'error',
                      message: ''
                        });
      }
      else if(response['err_code'] == -1){
        this.$message({
                      type: 'error',
                      message: ''
                        });
      }


    },
    beforefileUpload(){
      this.dataf['manid'] = this.watchId;
      this.dataf['suffix'] = this.suffix;
    },
      startnow(){
        this.$http.post('/api/startnow/',{'manId':this.watchId,'count':this.count},{emulateJSON:true})
              .then(function(response){
                 var res1 = JSON.parse(response.bodyText);
                 if(res1["err_code"] == 0){
                     this.$message({
                      type: 'success',
                      message: ''
                        });
                     this.hasstart = true;
                 }

              })
      },
      endnow(){
        this.$http.post('/api/endnow/',{'manId':this.watchId},{emulateJSON:true})
              .then(function(response){
                 var res1 = JSON.parse(response.bodyText);
                 if(res1["err_code"] == 0){
                     this.$message({
                      type: 'success',
                      message: ''
                        });
                     this.hasstart = false;
                 }

              })

      }

  }
}
</script>

<style>
  .el-header {
  background-color: rgb(254, 253, 252);
    width: 100%;
  }
  .el-avatar{

    vertical-align:middle;
  }
  #topNav{
    margin: 8px;
  }
  .row-tn {
    border-radius: 15px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    /*text-align: center;*/
    /*margin: auto auto;*/
  }
</style>



